<template>
  <div>
    <div class="indexMaintain-content-parent">
      <div class="indexMaintain-content" id="indexMaintain-content-super">
        <!--数据备份-->
        <div
          class="indexMaintain-content-backup"
          id="indexMaintain-content-backup"
          @click="pop_backup"
        ></div>
        <!--数据恢复-->
        <div
          class="indexMaintain-content-reset"
          id="indexMaintain-content-reset"
          @click="pop_reset"
        ></div>
        <!--税号变更-->
        <div class="indexMaintain-content-role" id="indexMaintain-content-role" @click="pop_role"></div>
        <!--用户管理-->
        <div class="indexMaintain-content-user" id="indexMaintain-content-user" @click="pop_user()"></div>
      </div>
    </div>
    <!-- 这是备份 提示的那个弹窗 -->
    <div id="pop-backup" class="layui-layer-wrap" style="display:none;">
      <div id="pop-backup-confirm" @click="closeAll"></div>
    </div>

    <!-- 这是数据恢复的那个弹窗 -->
    <div id="pop-reset" class="notice layui-layer-wrap" style="display: none;">
      <!--查找目录-->
      <div id="reset-category" onclick="reset_category()"></div>
      <!--查找文件-->
      <div id="reset-file" onclick="reset_file()"></div>
      <!--退出-->
      <div id="reset-exit" @click="closeAll"></div>
    </div>
    <!-- 这是税号变更的那个窗口 -->
    <div id="tax-change" class="notice layui-layer-wrap" style="display: none;"></div>

    <!--这是修改密码 添加用户的那个弹窗-->
    <div id="pop-user" class="layui-layer-wrap">
      <!--点击修改之前，只显示数据-->
      <!--头-->
      <!--默认显示的头：点击修改之前显示的头-->
      <div id="user-on-top" v-show="!isXiugai">
        <div id="on-top-add" class="on-top-add" @click="add_user"></div>
        <div id="on-top-change" class="on-top-add" @click="change_user"></div>
        <div id="on-top-delete" @click="UserDelete" class="on-top-add"></div>
        <div id="on-top-pwd" onclick="change_pwd()"></div>
      </div>
      <!--点击修改之后显示的头-->
      <div id="user-off-top" v-show="isXiugai">
        <div id="off-top-close" @click="change_cancel()"></div>
        <div id="off-top-save" @click="change_save()"></div>
      </div>
      <!--内容：点击修改之前，内容的input标签都不能用；点击修改之后，input标签可以使用-->
      <div class="user-content">
        <div class="on-content-left">
          <div id="on-content-left">
            <ul
              id="navigation"
              class="filetree treeview"
              style="margin: 25px 0px 0px 10px;width: 100px;max-height: 380px"
            >
              <li class="expandable lastExpandable">
                <div class="hitarea expandable-hitarea lastExpandable-hitarea"></div>
                <a>用户</a>
                <ul id="yonghu">
                  <li v-for="(item, i) in $store.state.user.userList" :key="i">
                    <span
                      class="chose_user_name"
                      style="cursor: pointer"
                      @click="MemberInfor(item)">{{item.username}}</span>
                  </li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
        <div class="content-right-name">
          <input
            type="text"
            id="content-right-name"
            onkeyup="value=value.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g,'')"
            onpaste="value=value.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g,'')"
            oncontextmenu="value=value.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g,'')"
            style="width: 272px;height: 20px"
            v-model="inputData.username"
            :readonly="!isXiugai"
          />
        </div>
        <div class="content-right-remark">
          <input
            type="text"
            id="content-right-remark"
            style="width: 272px;height: 20px"
            :readonly="!isXiugai"
            v-model="inputData.remark"
          />
        </div>
        <div id="content-right-time">{{$store.state.service.serviceData.month + '-01'}}</div>
        <div id="content-right-role">
          <div>
            <input
              type="radio"
              disabled="disabled"
              name="role"
              :checked="+inputData.type === 1?'checked':''"
            />管理员
          </div>
          <div>
            <input
              type="radio"
              disabled="disabled"
              name="role"
              :checked="+inputData.type === 2?'checked':''"
            />开票员
          </div>
        </div>
      </div>
    </div>

    <!-- 这是添加用户的那个弹窗 -->
    <div id="pop-add-user" class="layui-layer-wrap">
      <div class="user-name">
        <input
          type="text"
          id="newuser-name"
          style="width: 275px;height: 22px;"
          maxlength="5px"
          v-model="addUserForm.username"
        />
      </div>
      <div class="user-pwd">
        <input
          type="password"
          id="newuser-pwd"
          style="width: 275px;height: 22px"
          v-model="addUserForm.password"
        />
      </div>
      <!--修正备注文本域样式↓原始样式↑-->
      <div class="user-remark">
        <textarea
          id="BZ"
          style="resize:none;height: 35px; width: 273px;max-height: 35px;max-width: 273px;"
          v-model="addUserForm.remark"
        ></textarea>
      </div>
      <div class="user-people" id="newuser-username">{{$store.state.service.serviceData.user_name}}</div>
      <div class="user-time" id="newuser-time">{{$store.state.service.serviceData.lingpiao_time}}</div>
      <div class="user-role">
        <input type="radio" v-model="addUserForm.type" value="2" :checked="true" id="newuser-identity"/>开票员
      </div>
      <div class="user-btn">
        <input type="button" value="确定" @click="NewUserGenerated" class="btn-confirm"/>
      </div>
      <div class="user-btn-cancel" @click="btn_cancel"></div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        addPop: false,
        addUserForm: {
          type: '',
          token: '123456',
          password: '',
          username: '',
        },
        inputData: {},
        selectData: 0,
        isXiugai: false
      }
    },
    methods: {
      pop_backup () {
        layer.open({
          type: 1,
          title: ['提示', 'color:#ffffff;background-color:#315573;border-bottom:solid 1px #315573;'],
          area: ['400px'],  //宽高
          content: $('#pop-backup'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            $('#pop-backup').css('display', 'none')
          }
        })
      },
      pop_reset () {
        layer.open({
          type: 1,
          title: ['数据恢复', 'color:#000;background-color:#b2d4f2'],
          area: ['790px'],  //宽高
          content: $('#pop-reset'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            $('#pop-reset').css('display', 'none')
          }
        })
      },
      closeAll () {
        layer.closeAll()
      },
      pop_role () {
        layer.open({
          type: 1,
          title: ['税号变更', 'color:#ffffff;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['464px'],  //宽高
          content: $('#tax-change'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            // document.getElementById('pop-role').style.display = 'none';
          }
        })
      },
      pop_user () {
        layer.open({
          type: 1,
          title: ['用户管理', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['538px'],    //宽高
          content: $('#pop-user'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('pop-user').style.display = 'none'
          }
        })
      },
      add_user () {
        this.addPop = layer.open({
          type: 1,
          title: ['用户新增', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['366px'],    //宽高
          content: $('#pop-add-user'),    //内容
          resize: false,    //不允许拉伸
          shade: [0.1, '#3b6ea5'],    //遮罩阴影
          cancel: function () {
            document.getElementById('pop-add-user').style.display = 'none'
          }
        })
      },
      btn_cancel () {
        layer.close(this.addPop)
      },
      NewUserGenerated () {
        if (this.addUserForm.username === '') {
          this.$message.error('请填写用户名')
          return
        }

        if (this.addUserForm.password === '') {
          this.$message.error('请填写密码')
          return
        }

        if (this.addUserForm.type === '') {
          this.$message.error('请选择角色')
          return
        }
        this.$store.commit('addUser', this.addUserForm)
        this.addUserForm = {
          type: '',
          token: '123456',
          password: '',
          username: '',
        }
        layer.close(this.addPop)
      },
      MemberInfor (i) {
        this.inputData = i
      },
      UserDelete () {
        this.$store.commit('deleteUser', this.inputData)
        this.inputData = {}
      },
      change_user () {
        this.isXiugai = true
      },
      change_cancel () {
        this.isXiugai = false
      },
      change_save () {
        this.isXiugai = false
      }
    }
  }
</script>

